var _this
export default {
	data() {
		return {
			isFail:false,
			text2:'',
			isShow2:false,
			text:'是否确认收货',
			isShow:false,
			orderSn: "763363",
			orderInfo: {},
			data:{},
		}
	},
	onLoad(options) {
		this.orderSn = options.orderSn;
	},
	onShow() {
		_this=this
		this.getOrder()
		this.getList()
		uni.showLoading({
			title:'加载中....'
		})
	},
	methods: {
		config(res,index){
			console.log(res,index)
			_this.orderInfo.list[index].status=1
		},
		getList(){
			// _this.$http.getOrderList({order_sn: _this.orderSn}).then(res=>{
			// 	_this.list=res.data.result
			// })
		
		},
		fail(){
			this.isShow=false
		},
		succer(){
			let _this=this
			_this.$http.getConfirmOrder(_this.data).then(res => {
				let resData = res.data
				_this.$tool.getRouter(2).hideConfig()
				if (resData.success == 1) {
					// 走失败 
					_this.getOrder();
					console.log(resData)
					_this.$common.toast(resData.msg || '服务器异常')
				
				} else {
					// 走成功
					_this.$common.toast(resData.msg)
					setTimeout(()=>{
						uni.navigateTo()
					},200)
					return
				}
			}).catch(err=>{
				console.log(err)
				_this.$common.toast('服务器异常')
			})
		},
		succer2(){
			uni.navigateBack()
		},
		
		//识别取货码
		getOrder() {
			let _this = this;
			let data = {
				order_sn: _this.orderSn
			}
			// _this.$http.getOrder(data).then(res => {
			_this.$http.getOrderList(data).then(res => {
				console.log(res)
				let resData = res.data
				uni.hideLoading()
				if (resData.success == 1) {
					_this.orderInfo = resData.result
					console.log(resData)
					if(resData.result.order_status == 2){
						this.$tool.getRouter(2).showConfig(this.orderSn)
					}
				} else {
					_this.text2=resData.msg
					_this.isShow2=true
				}
			})
		},
		submit() {
			var _this = this;
			_this.data = {
				user_id: _this.orderInfo.user_id
			}
			
			_this.text='是否确认提货？'
			_this.isShow=true
			
		},
	}
}
